import React from 'react';
export class GDPSController extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            renderView: 'testConnection'
        };
    }
    async fetchWithTimeout(resource, timeout = 3000) {
        const controller = new AbortController();
        const id = setTimeout(() => controller.abort(), timeout);
        const response = await fetch(resource, {
            signal: controller.signal
        });
        clearTimeout(id);
        return response;
    }
    componentDidMount() {
        this.fetchWithTimeout(this.props.serverAddress + ':' + this.props.gdpsPort + '/ping', 1000).then(resp => {
            if (resp.ok) {
                this.setState({ renderView: 'successConnection' });
            }
            else {
                this.setState({ renderView: 'failedConnection' });
            }
        }, error => {
            this.setState({ renderView: 'failedConnection' });
        });
    }
    render() {
        if (this.state.renderView === 'testConnection') {
            return React.createElement("div", null, "Pinging GDPS...");
        }
        else if (this.state.renderView === 'successConnection') {
            return (React.createElement("div", null,
                React.createElement("p", null,
                    "GDPS is running happily on server ",
                    this.props.serverAddress),
                React.createElement("button", { onClick: this.props.handleExit }, "Ok")));
        }
        else if (this.state.renderView === 'failedConnection') {
            return (React.createElement("div", null,
                React.createElement("p", null, "Failed to reach GDPS. Would you like to (re)install it to the server?"),
                React.createElement("button", { onClick: () => this.setState({ renderView: 'gdpsInstaller' }) }, "Yes"),
                React.createElement("button", { onClick: this.props.handleExit }, "No")));
        }
        else if (this.state.renderView === 'gdpsInstaller') {
            return (React.createElement(GDPSInstaller, { handleExit: this.props.handleExit, serverAddress: this.props.serverAddress, jupyterApp: this.props.jupyterApp, hostOS: this.props.hostOS }));
        }
        else {
            return React.createElement("div", null, "Something went wrong. Please reload.");
        }
    }
}
class GDPSInstaller extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            renderView: 'intro',
            tg_output_path: '',
            local_output_path: '',
            isLocalInstall: props.serverAddress.includes('127.0.0.1') ||
                props.serverAddress.includes('localhost')
                ? true
                : false,
            sshUsername: '',
            sshKeyfile: '',
            tg_cluster_mode: false
        };
        this.handleVolumeMount = this.handleVolumeMount.bind(this);
        this.handleTmpOutput = this.handleTmpOutput.bind(this);
        this.handleInstall = this.handleInstall.bind(this);
        this.handleSSH = this.handleSSH.bind(this);
    }
    handleVolumeMount(state) {
        this.setState(state);
        this.setState({
            renderView: this.state.isLocalInstall ? 'toInstall' : 'ssh'
        });
    }
    handleTmpOutput(state) {
        this.setState(state);
        this.setState({
            renderView: this.state.isLocalInstall ? 'toInstall' : 'ssh'
        });
    }
    handleSSH(state) {
        this.setState(state);
        this.setState({ renderView: 'toInstall' });
    }
    handleInstall() {
        const ssh = ['ssh', '-o StrictHostKeyChecking=no'];
        if (this.state.sshKeyfile) {
            ssh.push('-i', '"' + this.state.sshKeyfile + '"');
        }
        ssh.push(this.state.sshUsername +
            '@' +
            this.props.serverAddress.replace('https://', '').replace('http://', ''));
        const appName = 'start_gdps_' + this.props.hostOS;
        const timestamp = new Date(Date.now());
        const cmds = [
            'mkdir -p ~/tg_gdps/logs;',
            'cd ~/tg_gdps;',
            'curl -O https://tigergraph-public-data.s3.us-west-1.amazonaws.com/ml-workbench/gdps/' +
                appName +
                ' && ',
            'chmod +x ' + appName + ';',
            '{ tg_output_path=' + this.state.tg_output_path.trim(),
            'local_output_path=' + this.state.local_output_path.trim(),
            'tg_cluster_mode=' + this.state.tg_cluster_mode.toString(),
            'nohup ./' +
                appName +
                ' >> logs/' +
                timestamp.toISOString() +
                '.log 2>&1 & };',
            'echo; echo; echo; echo Installation finished.'
        ];
        this.setState({ renderView: 'installing' });
        const exec = this.state.isLocalInstall
            ? cmds.join(' ') + '\n'
            : ssh.join(' ') +
                ' "bash -i -c ' +
                "'" +
                cmds.join(' ') +
                "'" +
                '"' +
                '\n';
        const commands = this.props.jupyterApp.commands;
        commands.execute('terminal:create-new').then(model => {
            const terminal = model.content;
            try {
                terminal.session.send({
                    type: 'stdin',
                    content: [exec]
                });
            }
            catch (e) {
                console.error(e);
                model.dispose();
            }
        });
    }
    render() {
        if (this.state.renderView === 'intro') {
            return (React.createElement("div", null,
                React.createElement("p", null, "To install GDPS onto this server, we first need to know how the TigerGraph database is set up there."),
                React.createElement("button", { type: "button", onClick: () => this.setState({ renderView: 'container' }) }, "Ok")));
        }
        else if (this.state.renderView === 'container') {
            return (React.createElement("div", null,
                React.createElement("p", null, "Is TigerGraph database running in a container?"),
                React.createElement("button", { type: "button", onClick: () => this.setState({ renderView: 'volumeMount' }) }, "Yes"),
                React.createElement("button", { type: "button", onClick: () => this.setState({ renderView: 'cluster' }) }, "No")));
        }
        else if (this.state.renderView === 'cluster') {
            return (React.createElement("div", null,
                React.createElement("p", null, "Is TigerGraph database running on a single machine or a cluster?"),
                React.createElement("button", { type: "button", onClick: () => this.setState({ tg_cluster_mode: false, renderView: 'tmpOutput' }) }, "Single"),
                React.createElement("button", { type: "button", onClick: () => this.setState({ tg_cluster_mode: true, renderView: 'tmpOutput' }) }, "Cluster")));
        }
        else if (this.state.renderView === 'volumeMount') {
            return React.createElement(VolumeMountForm, { update: this.handleVolumeMount });
        }
        else if (this.state.renderView === 'tmpOutput') {
            return React.createElement(TmpOutputForm, { update: this.handleTmpOutput });
        }
        else if (this.state.renderView === 'ssh') {
            return React.createElement(SSHForm, { update: this.handleSSH });
        }
        else if (this.state.renderView === 'toInstall') {
            return (React.createElement("div", null,
                React.createElement("p", null, "Thanks for providing all the information. A terminal will open on the right and install GDPS to the server. Continue?"),
                React.createElement("button", { onClick: this.handleInstall }, "Yes!"),
                React.createElement("button", { onClick: this.props.handleExit }, "hmm, maybe later")));
        }
        else if (this.state.renderView === 'installing') {
            return (React.createElement("div", null,
                React.createElement("p", null, "Installing GDPS. See the terminal on the right for progress."),
                React.createElement("button", { onClick: this.props.handleExit }, "Close")));
        }
        else {
            return React.createElement("div", null, "Something went wrong. Please reload.");
        }
    }
}
class VolumeMountForm extends React.Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit(event) {
        event.preventDefault();
        const target = event.target;
        const volumeMount = target.volumeMount.value;
        const [host_dir, container_dir] = volumeMount.split(':');
        this.props.update({
            local_output_path: host_dir.trim() + '/gdpstmp',
            tg_output_path: container_dir.trim() + '/gdpstmp'
        });
    }
    render() {
        return (React.createElement("form", { onSubmit: this.handleSubmit },
            React.createElement("p", null, "What is the argument after the -v flag (volume mount) when you started the container? (This info will be used by GDPS to read output from database)"),
            React.createElement("input", { type: "text", name: "volumeMount", placeholder: "/folder/on/host:/folder/in/container", defaultValue: "" }),
            React.createElement("button", { type: "submit" }, "Next")));
    }
}
class TmpOutputForm extends React.Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit(event) {
        event.preventDefault();
        const target = event.target;
        this.props.update({
            local_output_path: target.tmpOutput.value.trim(),
            tg_output_path: target.tmpOutput.value.trim()
        });
    }
    render() {
        return (React.createElement("form", { onSubmit: this.handleSubmit },
            React.createElement("p", null, "Where would you like the database to write temporary outputs? (Please make sure that both the database and your account have access to the folder.) If it doesn't exist, the folder will be created."),
            React.createElement("input", { type: "text", name: "tmpOutput", placeholder: "/home/tigergraph/tmp", defaultValue: "" }),
            React.createElement("button", { type: "submit" }, "Next")));
    }
}
class SSHForm extends React.Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit(event) {
        event.preventDefault();
        const target = event.target;
        this.props.update({
            sshUsername: target.username.value.trim(),
            sshKeyfile: target.keyfile.value.trim()
        });
    }
    render() {
        return (React.createElement("form", { onSubmit: this.handleSubmit },
            React.createElement("p", null, "Finally, we will use SSH to install GDPS onto the server. Please provide your SSH credentials (no sudo needed). If using password for SSH, leave the private key blank and you will be prompted to enter password next on the terminal."),
            React.createElement("input", { type: "text", name: "username", placeholder: "username", defaultValue: "" }),
            React.createElement("input", { type: "text", name: "keyfile", placeholder: "path to key file", defaultValue: "" }),
            React.createElement("br", null),
            React.createElement("button", { type: "submit" }, "Next")));
    }
}
