import React from 'react';
export class DBController extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            renderComponent: 'testConnection'
        };
    }
    async fetchWithTimeout(resource, timeout = 3000) {
        const controller = new AbortController();
        const id = setTimeout(() => controller.abort(), timeout);
        const response = await fetch(resource, {
            mode: 'no-cors',
            signal: controller.signal
        });
        clearTimeout(id);
        return response;
    }
    componentDidMount() {
        this.fetchWithTimeout(this.props.serverAddress + ':' + this.props.restPort + '/echo', 1000).then(resp => {
            window.open(this.props.serverAddress + ':' + this.props.gsqlPort);
            this.props.handleExit();
        }, error => {
            this.setState({ renderComponent: 'failedConnection' });
        });
    }
    render() {
        if (this.state.renderComponent === 'testConnection') {
            return React.createElement("div", null, "Pinging the database...");
        }
        else if (this.state.renderComponent === 'failedConnection') {
            if (this.props.hostOS === 'linux') {
                return (React.createElement("div", null,
                    React.createElement("p", null, "Failed to reach database. Would you like to (re)install it to the server?"),
                    React.createElement("button", { onClick: () => this.setState({ renderComponent: 'dbInstaller' }) }, "Yes"),
                    React.createElement("button", { onClick: this.props.handleExit }, "No")));
            }
            else {
                return (React.createElement("div", null,
                    React.createElement("p", null,
                        "Failed to reach database. Please check its status manually. If you haven't installed the database, try deploying a docker container to the server following the",
                        ' ',
                        React.createElement("a", { href: "https://docs.tigergraph.com/tigergraph-server/current/getting-started/docker", target: "_blank" }, "official guide"),
                        "."),
                    React.createElement("button", { onClick: this.props.handleExit }, "Back")));
            }
        }
        else if (this.state.renderComponent === 'dbInstaller') {
            return (React.createElement(DBInstaller, { handleExit: this.props.handleExit, hostOS: this.props.hostOS, serverAddress: this.props.serverAddress, jupyterApp: this.props.jupyterApp }));
        }
        else {
            return React.createElement("div", null, "Something went wrong. Please reload.");
        }
    }
}
class DBInstaller extends React.Component {
    constructor(props) {
        super(props);
        const isLocal = props.serverAddress.includes('127.0.0.1') ||
            props.serverAddress.includes('localhost')
            ? true
            : false;
        this.state = {
            renderView: isLocal ? 'toInstall' : 'ssh',
            isLocalInstall: isLocal,
            sshUsername: '',
            sshKeyfile: ''
        };
        this.handleInstall = this.handleInstall.bind(this);
        this.handleSSH = this.handleSSH.bind(this);
    }
    handleSSH(event) {
        const target = event.target;
        this.setState({
            sshUsername: target.username.value,
            sshKeyfile: target.keyfile.value,
            renderView: 'toInstall'
        });
    }
    handleInstall() {
        const host = this.state.sshUsername +
            '@' +
            this.props.serverAddress.replace('https://', '').replace('http://', '');
        const ssh = ['ssh', '-o StrictHostKeyChecking=no'];
        if (this.state.sshKeyfile) {
            ssh.push('-i', '"' + this.state.sshKeyfile + '"');
        }
        ssh.push(host);
        const cmds = [
            'mkdir -p ~/tg_tmp;',
            'cd ~/tg_tmp;',
            'curl -O https://dl.tigergraph.com/enterprise-edition/tigergraph-3.4.0-offline.tar.gz;',
            'tar xzf tigergraph-3.4.0-offline.tar.gz;',
            'cd tigergraph-3.4.0-offline;',
            'sudo ./install.sh -n ;',
            'cd;',
            'rm -rf tg_tmp;',
            'echo; echo; echo; echo Installation finished.'
        ];
        this.setState({ renderView: 'installing' });
        const exec = this.state.isLocalInstall
            ? cmds.join(' ') + '\n'
            : ssh.join(' ') + ' "' + cmds.join(' ') + '"' + '\n';
        const commands = this.props.jupyterApp.commands;
        commands.execute('terminal:create-new').then(model => {
            const terminal = model.content;
            try {
                terminal.session.send({
                    type: 'stdin',
                    content: [exec]
                });
            }
            catch (e) {
                console.error(e);
                model.dispose();
            }
        });
    }
    render() {
        if (this.state.renderView === 'ssh') {
            return (React.createElement("form", { onSubmit: this.handleSSH },
                React.createElement("p", null, "We will use SSH to install the database onto the server. Please provide your SSH credentials. `sudo` access is required. If using password for SSH, leave the private key blank and you will be prompted to enter password next on the terminal."),
                React.createElement("input", { type: "text", name: "username", placeholder: "username" }),
                React.createElement("input", { type: "text", name: "keyfile", placeholder: "path to key file" }),
                React.createElement("br", null),
                React.createElement("button", { type: "submit" }, "Next")));
        }
        else if (this.state.renderView === 'toInstall') {
            return (React.createElement("div", null,
                React.createElement("p", null, "A terminal will open on the right and install the database to the server. Continue?"),
                React.createElement("button", { onClick: this.handleInstall }, "Yes!"),
                React.createElement("button", { onClick: this.props.handleExit }, "hmm, maybe later")));
        }
        else if (this.state.renderView === 'installing') {
            return (React.createElement("div", null,
                React.createElement("p", null, "Installing Tigergraph database. See the terminal on the right for progress."),
                React.createElement("button", { onClick: this.props.handleExit }, "Close")));
        }
        else {
            return React.createElement("div", null, "Something went wrong. Please reload.");
        }
    }
}
